
interface ISquare {
    id: number;
    piece: "black" | "white" | "";
    square: string;
    "border-top": boolean
    "border-left": boolean
    "border-right": boolean
    "border-bot": boolean
    queen: boolean;
  }

export default function reverseChessboard(chessboard: ISquare[]) {
    const reversedChessboard: ISquare[] = [];
    for (let i = chessboard.length - 1; i >= 0; i--) {
        reversedChessboard.push(chessboard[i]);
    }

    return reversedChessboard;
}